import React, { useState } from 'react';
import { CardContent, Tab, Tabs } from '@material-ui/core';
import { useParams } from 'react-router-dom';
import { makeStyles } from '@material-ui/styles';
import _get from 'lodash.get';
import { gql, useMutation, useQuery } from '@apollo/client';
import { Formik, FormikHelpers } from 'formik';
import _isEmpty from 'lodash.isempty';
import MainCard from '../../../ui-component/cards/MainCard';
import { TabPanel } from '../../application/components/TabPanel';
import graphqlSchema from '../../../graphql';
import Loader from '../../../ui-component/Loader';
import Profile from '../../application/user/components/Profile';
import Security from '../../application/user/components/Security';
import Advanced from '../../application/user/components/Advanced';
import { useSnackBar } from '../../../hooks';
import { userValidationSchema } from '../../application/user/Validatation';
import { TEditUserProfile } from '../../../types';
import {
	updateUserCache,
	updateEnableUserCache,
	updateDisableUserCache,
} from '../../../dataProvider/cache';
import { convertPhoneNumber } from '../../../helpers';

const useStyles = makeStyles({
	toolbar: {
		'&&': {
			padding: 0,
		},
	},
	tab: {
		textTransform: 'none',
	},
	mainCard: {
		border: 'none',
	},
});

export const TABS = {
	profile: 'profile',
	security: 'security',
	advanced: 'advanced',
};

const EditPage = () => {
	const { id, tab: paramTab } = useParams<{ id: string; tab: string }>();
	const classes = useStyles();
	const showSnackBar = useSnackBar();
	const [tab, setTab] = useState(paramTab || TABS.profile);

	const { error, loading, data, refetch } = useQuery(
		gql(graphqlSchema.getUser),
		{
			variables: { UserId: id },
		}
	);
	const {
		loading: loadingUserGroups,
		error: userGroupsError,
		data: userGroupsData,
	} = useQuery(gql(graphqlSchema.users_UserGroups));
	const [doUpdateUserProfile, { error: updateProfileError }] = useMutation(
		gql(graphqlSchema.users_UpdateUserProfile),
		{
			update: updateUserCache,
		}
	);
	const [doRemoveUserGroup, { error: removingUserGroup }] = useMutation(
		gql(graphqlSchema.users_RemoveUserFromUserGroup)
	);
	const [doAddUserToGroup, { error: addingUserToGroupError }] = useMutation(
		gql(graphqlSchema.users_AddUserToUserGroup)
	);
	const [doDisableUser, { error: disableUserError, loading: isDisableUser }] =
		useMutation(gql(graphqlSchema.users_DisableUser), {
			update: updateDisableUserCache,
		});
	const [doEnableUser, { error: enableUserError, loading: isEnableUser }] =
		useMutation(gql(graphqlSchema.users_EnableUser), {
			update: updateEnableUserCache,
		});

	const handleChange = (event: React.ChangeEvent<{}>, newValue: string) => {
		setTab(newValue);
	};

	const user = _get(data, ['users_GetUserProfile']) || {};
	const groups = _get(userGroupsData, 'users_UserGroups');
	const firstName = _get(user, 'FirstName') || '';
	const lastName = _get(user, 'LastName') || '';
	const phone = _get(user, 'Phone') || '';
	const email = _get(user, 'Email') || '';
	const userId = _get(user, 'UserId');
	const userGroup = _get(user, ['UserGroups', '0', 'GroupName']);
	const isDisabled = !_get(user, 'Enabled', false);
	const title = `${firstName} ${lastName} ${isDisabled ? ' - Disabled' : ''}`;
	const initialValues = {
		firstName,
		lastName,
		email,
		phone,
		userGroup,
	};

	if (loading || loadingUserGroups) return <Loader />;

	if (
		userGroupsError ||
		error ||
		updateProfileError ||
		removingUserGroup ||
		addingUserToGroupError
	) {
		const message =
			_get(error, 'message') ||
			_get(updateProfileError, 'message') ||
			_get(removingUserGroup, 'message') ||
			_get(addingUserToGroupError, 'message') ||
			_get(disableUserError, 'message') ||
			_get(enableUserError, 'message') ||
			_get(userGroupsError, 'message', 'Something went wrong');
		showSnackBar({
			message,
		});
		return <></>;
	}

	const updateUserGroup = async (values: TEditUserProfile) => {
		const { userGroup: newUserGroup } = values;
		const isIgnoreUpdateUserGroup = newUserGroup === userGroup;
		if (isIgnoreUpdateUserGroup) return;

		const removeUserGroup = {
			UserId: userId,
			UserGroup: userGroup,
		};
		await doRemoveUserGroup({
			variables: removeUserGroup,
		});
		const addUserToGroup = {
			UserId: userId,
			UserGroup: newUserGroup,
		};
		await doAddUserToGroup({
			variables: addUserToGroup,
		});
	};

	const onSubmit = async (
		values: TEditUserProfile,
		{ setStatus, setSubmitting }: FormikHelpers<TEditUserProfile>
	) => {
		try {
			const { firstName, lastName, phone } = values;
			const variables = {
				UserId: userId,
				FirstName: firstName,
				LastName: lastName,
				Phone: _isEmpty(phone) ? undefined : convertPhoneNumber(phone),
			};
			await updateUserGroup(values);
			await doUpdateUserProfile({
				variables,
			});
			showSnackBar({
				message: 'Profile has updated',
				alertSeverity: 'success',
			});
			refetch();
		} catch (e: any) {
			const message = _get(e, 'message', 'Something went wrong');
			setStatus({ success: false });
			setSubmitting(false);
			showSnackBar({ message });
		}
	};

	const onUpdateUserStatus = async () => {
		const variables = {
			UserId: userId,
		};
		if (isDisabled) {
			await doEnableUser({ variables });
			showSnackBar({
				message: 'User has enabled',
				alertSeverity: 'success',
			});
			refetch();
			return;
		}

		await doDisableUser({
			variables,
		});
		showSnackBar({
			message: 'User has disabled',
			alertSeverity: 'success',
		});
		refetch();
	};

	return (
		<MainCard title={title} sx={{ mb: 3 }} contentClass={classes.toolbar}>
			<Formik
				initialValues={initialValues}
				onSubmit={onSubmit}
				validationSchema={userValidationSchema}
			>
				{({ handleSubmit }) => (
					<form noValidate onSubmit={handleSubmit}>
						<Tabs
							value={tab}
							sx={{ px: 3 }}
							variant='scrollable'
							onChange={handleChange}
						>
							<Tab
								value={TABS.profile}
								label='Profile'
								classes={{ root: classes.tab }}
							/>
							<Tab
								value={TABS.security}
								label='Security'
								classes={{ root: classes.tab }}
							/>
							<Tab
								value={TABS.advanced}
								label='Advanced'
								classes={{ root: classes.tab }}
							/>
						</Tabs>
						<TabPanel value={tab} index={TABS.profile}>
							<MainCard content={false} className={classes.mainCard}>
								<CardContent>
									<Profile isGroupDisable={false} groups={groups} />
								</CardContent>
							</MainCard>
						</TabPanel>
						<TabPanel value={tab} index={TABS.security}>
							<MainCard content={false} className={classes.mainCard}>
								<CardContent>
									<Security
										onClick={onUpdateUserStatus}
										isDisabled={isDisabled}
										isSaving={isDisableUser || isEnableUser}
									/>
								</CardContent>
							</MainCard>
						</TabPanel>
						<TabPanel value={tab} index={TABS.advanced}>
							<MainCard content={false} className={classes.mainCard}>
								<CardContent>
									<Advanced user={user} />
								</CardContent>
							</MainCard>
						</TabPanel>
					</form>
				)}
			</Formik>
		</MainCard>
	);
};

export default EditPage;
