import {
	Grid,
	List,
	ListItem,
	ListItemText,
	Typography,
} from '@material-ui/core';
import React from 'react';
import _get from 'lodash.get';
import { gql, useLazyQuery } from '@apollo/client';
import { TUser } from '../../../../types';
import { gridSpacing } from '../../../../store/constant';
import { formatDate } from '../../../../helpers';
import graphqlSchema from '../../../../graphql';
import Loader from '../../../../ui-component/Loader';
import { useSnackBar } from '../../../../hooks';
import SubCard from '../../../../ui-component/cards/SubCard';

type Props = {
	user: TUser;
};

const Advanced = ({ user }: Props) => {
	const showSnackBar = useSnackBar();
	const userId = _get(user, 'UpdatedBy');
	const [getUserProfile, { loading, data, error }] = useLazyQuery(
		gql(graphqlSchema.getUser)
	);

	React.useEffect(() => {
		if (!userId) return;
		getUserProfile({
			variables: { UserId: userId },
		});
	}, [userId]);

	const updateByUser = _get(data, ['users_GetUserProfile']) || {};
	const firstName = _get(updateByUser, 'FirstName') || '';
	const lastName = _get(updateByUser, 'LastName') || '';
	const name = `${firstName} ${lastName}`;

	if (loading) return <Loader />;

	if (error) {
		const message = _get(error, 'message', 'Something went wrong');
		showSnackBar({
			message,
		});
		return <></>;
	}

	return (
		<Grid container spacing={gridSpacing}>
			<Grid item sm={6} md={6}>
				<SubCard title='Advanced'>
					<List>
						<ListItem sx={{ px: 0, py: 1 }}>
							<ListItemText sx={{ minWidth: { xs: '50%', sm: 'auto' } }}>
								Created At
							</ListItemText>
							<Typography
								variant='body2'
								sx={{ textOverflow: 'ellipsis', overflow: 'hidden' }}
							>
								{formatDate(_get(user, 'CreatedAt'))}
							</Typography>
						</ListItem>
						<ListItem sx={{ px: 0, py: 1 }}>
							<ListItemText sx={{ minWidth: { xs: '50%', sm: 'auto' } }}>
								Updated At
							</ListItemText>
							<Typography
								variant='body2'
								sx={{ textOverflow: 'ellipsis', overflow: 'hidden' }}
							>
								{formatDate(_get(user, 'UpdatedAt'))}
							</Typography>
						</ListItem>
						<ListItem sx={{ px: 0, py: 1 }}>
							<ListItemText sx={{ minWidth: { xs: '50%', sm: 'auto' } }}>
								Created By
							</ListItemText>
							<Typography
								variant='body2'
								sx={{ textOverflow: 'ellipsis', overflow: 'hidden' }}
							>
								{_get(user, 'CreatedBy')}
							</Typography>
						</ListItem>
						<ListItem sx={{ px: 0, py: 1 }}>
							<ListItemText sx={{ minWidth: { xs: '50%', sm: 'auto' } }}>
								Updated By
							</ListItemText>
							<Typography
								variant='body2'
								sx={{ textOverflow: 'ellipsis', overflow: 'hidden' }}
							>
								{name}
							</Typography>
						</ListItem>
						<ListItem sx={{ px: 0, py: 1 }}>
							<ListItemText sx={{ minWidth: { xs: '50%', sm: 'auto' } }}>
								User Status
							</ListItemText>
							<Typography
								variant='body2'
								sx={{ textOverflow: 'ellipsis', overflow: 'hidden' }}
							>
								{_get(user, 'UserStatus')}
							</Typography>
						</ListItem>
					</List>
				</SubCard>
			</Grid>
		</Grid>
	);
};

export default Advanced;
