import React from 'react';
import { Button, Grid, Stack, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import SubCard from '../../../../ui-component/cards/SubCard';
import AnimateButton from '../../../../ui-component/extended/AnimateButton';
import { gridSpacing } from '../../../../store/constant';

const useStyle = makeStyles(theme => ({
	button: {
		color: theme.palette.error.dark,
		borderColor: theme.palette.error.dark,
	},
}));

type Props = {
	isDisabled: boolean;
	onClick: () => void;
	isSaving: boolean;
};

const Security = ({ onClick, isDisabled, isSaving }: Props) => {
	const classes = useStyle();
	const title = isDisabled ? 'Disabled Account' : 'Delete Account';
	const buttonLabel = isDisabled ? 'Enable Account' : 'Disable Account';
	return (
		<Grid container spacing={gridSpacing}>
			<Grid item sm={6} md={4}>
				<Grid container spacing={gridSpacing}>
					<Grid item xs={12}>
						<SubCard title={title}>
							<Grid container spacing={2}>
								<Grid item xs={12}>
									<Typography variant='body1'>
										Disabled accounts will no longer have access to Agent Portal
									</Typography>
								</Grid>
								<Grid item xs={12}>
									<Stack direction='row'>
										<AnimateButton>
											<Button
												variant='outlined'
												size='small'
												onClick={onClick}
												disabled={isSaving}
												className={classes.button}
											>
												{buttonLabel}
											</Button>
										</AnimateButton>
									</Stack>
								</Grid>
							</Grid>
						</SubCard>
					</Grid>
				</Grid>
			</Grid>
		</Grid>
	);
};

export default Security;
